// Define typography



        *{
          box-sizing: border-box;
          padding: 0;
          margin: 0;
        }


// Vertically center anything

    .vertical-center {
      display: flex;
      align-items: center;
      justify-content: center;
    }



// Grid

@supports (grid-area: auto) {
        .container {
          display: grid;
          grid-gap: 1rem;
          width: 100vw;
          height: 100vh;
        }
    
@media (min-width:27em) {
    .site {
			position: relative;
			left: -14em;
			overflow-x: hidden;
			display: grid;
			grid-template-columns: 15em calc(100% - 1em);
			grid-template-areas:
				"nav intro"
				"nav portfolio"
				"nav aboutskills"
				"nav experience"
                "nav contact"
                "nav footer";
			transition: left .6s;
		}

		.reveal {
			left: 0;
		}

		.intro {
			grid-area: intro;
		}

		.portfolio {
			grid-area: portfolio;
		}

		.about-skills {
			grid-area: aboutskills;
		}

		.experience {
			grid-area: experience;
		}
    
        .contact {
			grid-area: contact;
		}
    
        .footer {
            grid-area: footer;
        }

		.main-nav {
			grid-area: nav;
		}

		.main-nav ul {
			/*position: fixed;*/
			display: none;
			flex-direction: column;
		}

		.open ul {
			display: flex;
		}

		.trigger {
			position: fixed;
			display: block;
			top: 1.5em;
			left: .2em;
			transition: left .6s;
		}

		.reveal .trigger {
			left: 14em;
		}
    }
    
    
@media (min-width:78em) {
        .container {
          grid-template-columns: 200px repeat(5, 1fr);
          grid-template-rows: repeat(5, 1fr) 50px;
        }
    
        .intro {
          grid-row: 1 / 4;
          grid-column: 1 / 3;
        }

        .portfolio {
          grid-row: 1 / 4;
          grid-column: 3 / 7;
          display: grid!important;
          grid-gap: 10px;
          grid-template-columns: repeat(3, 1fr);
          grid-template-rows: repeat(4, 1fr);
        }
        
        .portfolio-item {
            width:100%;
            height:100%;
        }
        
        .portfolio .a {
            grid-column: 1 / 2;
            grid-row: 1/3;
        }
        
        .portfolio .b {
            grid-column: 2 / 4;
            grid-row: 1/2;
        }
        
        .portfolio .c {
            grid-column: 1 / 2;
            grid-row: 3/4;
        }
        
        .portfolio .d {
            grid-column: 2 / 3;
            grid-row: 2/4;
        }
        
        .portfolio .e {
            grid-column: 1 / 3;
            grid-row: 4/5;
        }
        
        .portfolio .f {
            grid-column: 3 / 4;
            grid-row: 2/5;
        }

        .about {
          grid-row: 4 / 5;
          grid-column: 1 / 4;
        }

        .skills {
          grid-row: 4 / 5;
          grid-column: 4 / 7;
        }
        
        .experience {
          grid-row: 5 / 6;
          grid-column: 1 / 7;
        }

        .footer {
          grid-row: 6 / 7;
          grid-column: 1 / 7;
        }
}

        
// Portfolio Items Grid

@media (min-width:767px) {
        .item {
          background-color: #1EAAFC;
          background-image: linear-gradient(130deg, #6C52D9 0%, #1EAAFC 85%, #3EDFD7 100%);
          box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
          color: #ffffff;
          border-radius: 4px;
          border: 6px solid #171717;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 18px;
          font-weight: bold;
        }
    }
}

